</template>
<script>
import left from './../components/left'
import { dataRequest as api } from '@/api/api'
export default {
	name: 'name',
	components: {
		// 组件
		left,
	},
	data() {
		return {
			//返回值
			loading: true,
			title: '',
			description: '我们提供专业的区块链培训和开发工具服务，学习区块链技术，获取区块链工具源码，欢迎加入区块链开发社区。',
			arclist: [],
			tesorting: 0,
			count: 5,
			qzsolldav: 'qzsolldav',
			tmime: true,
		}
	},
	methods: {
		Updatesorting() {
			this.tmime = !this.tmime
			this.arclist = this.arclist.sort((a, b) => {
				//
				if (this.tesorting == 0) {
					return new Date(b.Creationtime).getTime() / 1000 - new Date(a.Creationtime).getTime() / 1000
				} else if (this.tesorting == 1) {
					if (this.tmime) {
						return new Date(b.Creationtime).getTime() / 1000 - new Date(a.Creationtime).getTime() / 1000
					} else {
						return new Date(a.Creationtime).getTime() / 1000 - new Date(b.Creationtime).getTime() / 1000
					}
				}
			})
			let arr = []
			for (let i = 0; i < this.count && i < this.arclist.length; i++) {
				arr.push(this.arclist[i].id)
			}
			api(['post=acbrowsing', arr])
		},
		// 函数
		// 获取文章
		async getdata() {
			let array = await api(['post=getarticle', []])
			array = array.data
			this.arclist = array
			this.Updatesorting()
		},
		// 新增浏览量
		setvivw() {
			let arr = []
			for (let i = this.count - 2; i < this.count; i++) {
				arr.push(this.arclist[i].id)
			}
			api(['post=acbrowsing', arr])
		},
	},
	mounted() {
		// 生命周期:
		this.getdata()
	},
	updated() {
		this.loading = false
	},
}
</script>
