<template>
	<div class="QAHead" v-loading.fullscreen.lock="loading">
		<div class="paddingtop10 columnnav Horizontalspacing minixgap fonfff minfont">
			<el-breadcrumb separator-class="el-icon-arrow-right">
				<el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
				<el-breadcrumb-item v-for="(item, index) in nav" :key="index" :to="{ path: '/column/' + item.alias + '.html' }" @click="get()">{{ item.name }}</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="questionnav line">
			<div class="columnLayout interval">
				<span class="fonfff center QAfont">{{ current.name }}</span>
				<div class="Horizontalspacing interval fonfff">
					<div class="Horizontalspacing minxgap">
						<div class="iconfont icon-duigouxiao fonfff minfont center"></div>
						<div>学习技能、解决问题学习技能、解决问题学习技能、解决问题学习技能、解决问题学习技能、解决问题</div>
					</div>
				</div>
			</div>
		</div>
	</div>
	<div class="main">
		<div>
			<div class="content">
				<div class="Genericpadding Horizontalspacing interval">
					<div class="QAgreybox" @click="actr(current.alias)">全部</div>
					<div class="QAgreybox" v-for="(item, index) in subordinate" :key="index" @click="actr(item.alias)">{{ item.name }}</div>
					<!-- <div class="QAgreybox">合约问题</div>
					<div class="QAgreybox">疑难技术</div> -->
				</div>
				<div class="Genericpadding columnLayout">
					<div class="QArectangle">
						<div class="Horizontalspacing interval">
							<div class="qzsolld" :class="{ qzsolldav: tesorting == 0 }" @click=";((tesorting = 0), (order = false)), Updatesorting()">最新文章</div>
							<div class="qzsolld" :class="{ qzsolldav: tesorting == 1 }" @click=";(tesorting = 1), Updatesorting()">按照時間</div>
							<div class="qzsolld" :class="{ qzsolldav: tesorting == 2 }" @click=";(tesorting = 2), Updatesorting()">按照人氣</div>
							<div class="qzsolld" :class="{ qzsolldav: tesorting == 3 }" @click=";(tesorting = 3), Updatesorting()">按照評論</div>
						</div>
					</div>
					<ul class="columnLayout">
						<router-link v-for="(item, index) in arclist.slice(index, index + pagesize)" :key="index" :to="'/article/' + item.alias + '/' + item.id + '.html'">
							<li class="Genericpadding Horizontalspacing interval">
								<div>
									<el-image class="PostThumbnails" :src="item.thumbnail ? item.thumbnail : ''" lazy>
										<template #error>
											<div class="image-slot iconfont icon-jiazaitupian"></div>
										</template>
									</el-image>
								</div>
								<div class="ArticleList">
									<div class="Horizontalspacing minxgap ArticleTitlep">
										<div class="smallsquare bold"><span class="center">热门</span></div>
										<span class="turngreen">{{ item.title + index }}</span>
									</div>
									<span class="minfont grey Widthlimit">{{ item.description }}</span>
									<div class="articlebt minfont">
										<div class="Horizontalspacing interval">
											<div class="Horizontalspacing minixgap">
												<img class="pimgmins" src="./../assets/img/avatar.png" alt="" />
												<span class="center">{{ item.username }}</span>
											</div>
											<div class="grey center">{{ item.pidname }}</div>
										</div>
										<div class="Horizontalspacing interval grey">
											<div class="Horizontalspacing minixgap">
												<span class="center">日期:</span>
												<span class="center">{{ item.Creationtime }}</span>
											</div>
											<div class="Horizontalspacing minixgap">
												<span class="center">点赞:</span>
												<span class="center">1000</span>
											</div>
											<div class="Horizontalspacing minixgap">
												<span class="center">浏览:</span>
												<span class="center">2000</span>
											</div>
										</div>
									</div>
								</div>
							</li>
						</router-link>
					</ul>
					<el-pagination background class="centerend" :hide-on-single-page="value" @current-change="fun" :current-page="curPage" :page-size="pagesize" layout="prev, pager, next" :total="total"> </el-pagination>
				</div>
			</div>
		</div>
		<div>
			<left></left>
		</div>
	</div>
</template>
<script>
import left from './../components/left'
import { dataRequest as api } from '@/api/api'
export default {
	name: 'name',
	components: {
		// 组件
		left,
	},
	data() {
		return {
			comn: this.$route.params.comn.replace(/.html/g, ''),
			arclist: [],
			nav: [],
			current: [],
			subordinate: [],
			loading: true,
			qzsolldav: `qzsolldav`,
			tesorting: 0,
			order: false,

			//返回值
			value: true,
			pagesize: 10, //每页显示数量
			curPage: 1, // 当前页数
			total: 0, // 一共几条数据
			index: 0, //开始页码
		}
	},
	methods: {
		// 更新排序
		Updatesorting() {
			this.order = !this.order
			// let arclist = this.arclist
			this.arclist = this.arclist.sort((a, b) => {
				//
				if (this.tesorting == 0 || this.tesorting == 1) {
					if (this.order) {
						return new Date(b.Creationtime).getTime() / 1000 - new Date(a.Creationtime).getTime() / 1000
					} else {
						return new Date(a.Creationtime).getTime() / 1000 - new Date(b.Creationtime).getTime() / 1000
					}
				}
			})
		},
		//默认 函数
		async get() {
			let comn = await api(['post=Obtainclassification', this.comn])
			this.nav = comn.data
			//获取下级分类
			let comnlist = await api(['post=Getification', this.comn])
			this.current = comnlist['data'][0]
			this.subordinate = comnlist['data'][1]
			this.actr()
		},
		// 获取文章
		async actr(ele = '') {
			if (ele) {
				this.comn = ele
			} else {
				this.comn = this.$route.params.comn.replace(/.html/g, '')
			}
			let array = await api(['post=getarticle', { alias: this.comn }])
			if (array.code == 200) {
				array = array.data
				this.arclist = array
				this.total = this.arclist.length
				this.Updatesorting()
			}
		},
		// 分页
		fun(ele = 1) {
			this.curPage = ele
			this.index = (this.curPage - 1) * this.pagesize
			window.scrollTo({ behavior: 'smooth', top: 10 })
			let arr = []

			for (let i = this.index; i < this.index + this.pagesize && i < this.qalist.length; i++) {
				arr.push(this.qalist[i].id)
			}
			api(['post=acbrowsing', arr])
		},
	},

	mounted() {
		// 生命周期:
		this.get()
	},
	updated() {
		this.loading = false
	},
}
</script>
<style scoped>
.image-slot {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 200px;
	height: 150px;
	background: var(--el-fill-color-light);
	color: var(--el-text-color-secondary);
	font-size: 30px;
}
.image-slot {
	font-size: 30px;
}
.Widthlimit {
	width: 600px;
	max-height: 5.8125rem;
	overflow: hidden;
}

/* 不被选中时的颜色 */

/* 不被选中时的颜色 */
/* .el-breadcrumb__item ::v-deep .el-breadcrumb__inner {
	color: #fff;
	font-weight: 400;
} */
/* 被选中时的颜色 */
/* .el-breadcrumb__item:last-child ::v-deep .el-breadcrumb__inner {
	color: rgb(175, 175, 175);
	font-weight: 800;
}
.el-breadcrumb__inner ::v-deep .el-breadcrumb__separator {
	color: #fff;
} */
.qzsolldav {
	color: var(--primary-color);
}
</style>
