// 定义类型别名来描述用户信息的结构
/**
 * @typedef {Object} UserInformation
 * @property {string} username - 用户名
 * @property {string} password - 密码
 */
async function dataRequest(data) {
  try {
    const userinformation = {
      username: getCookie('username'),
      password: getCookie('password'),
    };

    if (typeof data[1] == 'object') {
      data[1].userinformation = userinformation;
    }

    const response = await fetch('https://api.dexc.pro/api.php?' + data[0], {
      method: 'POST',
      mode: 'cors',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8',
        'Content-Length': JSON.stringify(data[1]).length.toString(),
      },
      body: JSON.stringify(data[1]),
    });

    if (!response.ok) {
      throw new Error('Network response was not ok');
    }

    const returndata = await response.json();
    return returndata;
  } catch (error) {
    console.error(error);
    return false;
  }
}

async function filesRequest(data) {
  try {
    const controller = new AbortController();
    const id = setTimeout(() => controller.abort(), 10000);
    console.log('开始上传');
    let response = await fetch('https://api.dexc.pro/api.php?' + data[0], {
      method: 'POST',
      signal: controller.signal,
      body: data[1],
    });
    console.log('结束');
    const games = await response.json();
    clearTimeout(id);
    return games;
  } catch (error) {
    return { code: 408, data: error, massage: '超时', state: 'error' };
  }
}

async function fileUploadRequest(ele, quill) {
  let userinformation = {
    username: getCookie('username'),
    password: getCookie('password'),
  };

  var formData = new FormData();
  formData.append('file', ele.file);
  formData.append('user', JSON.stringify(userinformation));
  let a = await filesRequest(['post=files', formData]);
  const { index, length } = quill.selection.savedRange;
  const selectedText = quill.getText(index, length);
  const newText = selectedText.toUpperCase();
  quill.deleteText(index, length);
  quill.insertText(index, newText);
  quill.setSelection(index, newText.length);
  console.log(a);
  if (a.code == 200) {
    quill.clipboard.dangerouslyPasteHTML(index, `<img src ="` + a.data + `" alt = "" >`);
    return true;
  } else {
    return false;
  }
}

async function loginRequest(username, password) {
  setCookie('username', username, 60);
  setCookie('password', password, 60);
  return await automaticLogin();
}

function setText(val) {
  if (val != null && val != "") {
    var re1 = new RegExp("<.+?>|&.+?;", "g");
    var msg = val.replace(re1, "");
    msg = msg.replace(/\s/g, "");
    msg = msg.replace(/[\r\n]/g, "");
    return msg.substr(0, 100);
  } else return '';
}

async function automaticLogin() {
  console.log(getCookie('username'), getCookie('password'))
  if (getCookie('username') && getCookie('password')) {
    let array = await dataRequest(['post=login', {
      username: getCookie('username'),
      password: getCookie('password'),
    }]);

    return array.data ? array.data : false;
  } else {
    return false
  }
}

function setCookie(cname, cvalue = '', exdays = -100) {
  const d = new Date();
  d.setTime(d.getTime() + exdays * 24 * 60 * 60 * 1000);
  const expires = "expires=" + d.toUTCString();
  document.cookie = cname + "=" + cvalue + "; " + expires;
}

function getCookie(cname) {
  const name = cname + "=";
  const ca = document.cookie.split(";");
  for (let i = 0; i < ca.length; i++) {
    const c = ca[i].trim();
    if (c.indexOf(name) === 0) return c.substring(name.length, c.length);
  }
  return "";
}

