<template
	><main v-my-directive :data-title="title" :data-description="description">
		<div v-loading.fullscreen.lock="loading" class="QAacHead">
			<div class="QAacnav articlebt line">
				<div class="columnLayout interval">
					<div class="Horizontalspacing interval minfont">
						<div class="Horizontalspacing minixgap">
							<img class="pimgmins" src="./../assets/img/avatar.png" alt="" />
							<span class="center">{{ content.username }}</span>
						</div>
						<div class="center grey">{{ content.Creationtime }}</div>
						<div class="center grey">提问:</div>
					</div>
					<span class="maxqcfont">{{ content.title }}</span>
					<div class="Horizontalspacing interval minfont">
						<div class="qzbuttons"> <div>+</div><span>写回答</span> </div>
						<div class="qathumbs-up">
							<div class="iconfont icon-31dianzan"></div>
							<div>好问题</div>
							<div>999</div>
						</div>
						<div class="Horizontalspacing minixgap center">
							<div class="redborad">状态</div>
							<div class="center">问题有人回答啦，大家来学习一下吧~</div>
						</div>
						<div class="Horizontalspacing minixgap center">
							<div class="iconfont icon-dashangzonge red"></div>
							<div class="center"
								>悬赏<span class="red">{{ content.Price }}</span
								>元已解决</div
							>
						</div>
					</div>
				</div>
				<div class="Horizontalspacing minixgap center"
					><span>浏览</span><span> {{ content.PageView }}</span></div
				>
			</div>
		</div>
		<div class="main">
			<div>
				<div class="content">
					<!--  -->
					<div class="Genericpadding columnLayout">
						<div class="QArectangle articlebt">
							<div class="qzsolld qzsolldav Green">最新问题</div>
						</div>
						<!-- 文章内容 -->
						<div class="columnLayout interval">
							<div v-html="content.content"> </div>
							<!-- 采纳 -->
							<div class="abstracts columnLayout interval">
								<div class="articlebt">
									<div class="Horizontalspacing minaxgap red bold">
										<div class="iconfont icon-dianzan_kuai maxqcfont"></div>
										<div>最佳答案</div>
									</div>
									<div class="Horizontalspacing interval minfont">
										<div class="Horizontalspacing minixgap">
											<img class="pimgmins center" src="./../assets/img/avatar.png" alt="" />
											<span class="center">用户昵称</span>
										</div>
										<div class="Horizontalspacing minixgap center">
											<div class="iconfont icon-dashangzonge red"></div>
											<div class="center grey"
												>悬赏<span class="red">{{ content.Price }}</span
												>元已解决</div
											>
										</div>
									</div>
								</div>
								<div>我知道！我知道！ https://www.ceotheme.com/1039.html 这里有详细的安装教程！ 望采纳！！</div>
							</div>
							<div class="abstract Horizontalspacing minxgap grey minifont">
								<div class="iconfont icon-gantanhao-yuankuang"></div>
								<div>版权：言论仅代表个人观点，不代表官方立场。转载请注明出处：{{ domain }}</div>
							</div>
						</div>
					</div>
					<!-- 回答问题 -->
					<div class="ThinLineLayout">
						<div class="Genericpadding articlebt">
							<div>发表回答</div>
							<div class="minfont grey">5条评论</div>
						</div>
						<div class="Genericpadding Horizontalspacingg interval">
							<img src="./../assets/img/avatar.png" alt="" class="pimg" />
							<div class="Intervalvertical">
								<div>
									<div>
										<!-- 富文本 -->
										<dexedi height="100px"></dexedi>
									</div>
								</div>
								<div class="buttons" @click="get">提交</div>
							</div>
						</div>
						<div class="Genericpadding Intervalvertical">
							<!-- 评论 -->
							<div class="articlebt minfont">
								<div class="Horizontalspacing minxgap"
									><img src="./../assets/img/avatar.png" alt="" class="pimgmin" />
									<p class="center">一位评论者</p>
								</div>
								<div class="Horizontalspacing minxgap grey">
									<p>时间</p>
									<div>回复</div>
								</div>
							</div>
							<div class="abstracts pmaring">
								<p>评论内容</p>
							</div>
							<!-- 回复列表 -->
							<ul class="pmaring Intervalvertical">
								<div class="Intervalvertical">
									<div class="articlebt minfont">
										<div class="Horizontalspacing minxgap"
											><img src="./../assets/img/avatar.png" alt="" class="pimgmins" />
											<p class="center">一位评论者</p>
										</div>
										<div class="Horizontalspacing minxgap grey">
											<p>时间</p>
											<div>回复</div>
										</div>
									</div>
									<div class="abstracts pmaring">
										<p>评论内容</p>
									</div>
								</div>
								<div class="Intervalvertical">
									<div class="articlebt minfont">
										<div class="Horizontalspacing minxgap"
											><img src="./../assets/img/avatar.png" alt="" class="pimgmins" />
											<p class="center">一位评论者</p>
										</div>
										<div class="Horizontalspacing minxgap grey">
											<p>时间</p>
											<div>回复</div>
										</div>
									</div>
									<div class="abstracts pmaring">
										<p>评论内容</p>
									</div>
								</div>
								<!-- 这里插入 收起回复  隐藏当前 显示更多 -->
							</ul>
							<!-- 第二条评论 -->
							<div class="articlebt minfont">
								<div class="Horizontalspacing minxgap"
									><img src="./../assets/img/avatar.png" alt="" class="pimgmin" />
									<p class="center">一位评论者</p>
								</div>
								<div class="Horizontalspacing minxgap grey">
									<p>时间</p>
									<div>回复</div>
								</div>
							</div>
							<div class="abstracts pmaring">
								<p>评论内容</p>
							</div>
						</div>

						<div class="Genericpadding">
							<div class="abstracts">
								<p class="center">还没有回答呢，快来抢沙发~</p>
							</div>
						</div>
					</div>
				</div>
			</div>
			<div>
				<qaleft></qaleft>
			</div>
		</div>
	</main>
</template>
<script>
import qaleft from './../components/qaleft'
import dexedi from './../components/dexedi'
import { dataRequest as api } from '@/api/api'
import { QuillEditor } from '@vueup/vue-quill'
import hljs from 'highlight.js'
export default {
	name: 'name',
	components: {
		// 组件
		qaleft,
		dexedi,
		QuillEditor,
	},
	data() {
		return {
			loading: true,
			domain: window.location.href,
			article: '',
			content: [],
			title: '',
			description: '',
			//返回值
		}
	},
	methods: {
		// 接受富文本
		edi_art(value) {
			this.article = value
		},

		async get() {
			let articid = this.$route.params.index.replace(/.html/g, '')
			let array = await api(['post=getquestions', { id: articid }])
			console.log(array)
			if (array.code == 200) {
				this.content = array.data[0]
				this.title = this.content.title
				this.description = modulega.setText(this.content.content)
				this.content.Price = (Math.round(this.content.Price * 1000) / 1000).toString()
			}
		},
	},
	mounted() {
		// 生命周期:
		this.get()
	},
	updated() {
		this.loading = false
	},
}
</script>
