<script>
import qaleft from './../components/qaleft'
import Quill from 'quill'
export default {
	name: 'name',
	components: {
		// 组件

		qaleft,
	},
	data() {
		return {
			//返回值
			edi: `<div id="qaeditors"></div>`,
			list: 0,
		}
	},
	methods: {
		// 函数
		fun() {
			this.list = this.list + 1
			console.log(this.list)
			var toolbarOptions = [
				[
					{
						align: [],
					},
					'code-block',
					'image',
				], // toggled buttons
			]
			var quills = new Quill('#qaeditors', {
				modules: {
					toolbar: toolbarOptions,
				},
				theme: 'snow',
			})
			var delta = quills.getContents()
			var toolbar = quills.getModule('toolbar')
			toolbar.addHandler('image', xiugai)
			//监听并修改文章
			function xiugai() {
				const text = 'hello'
				let index = quills.selection.savedRange.index
				quills.clipboard.dangerouslyPasteHTML(index, `<img src = "` + text + `" alt = "" >`)
			}
			// 设置富文本高度
			document.getElementById('qaeditors').style.minHeight = '300px'
		},
	},
	mounted() {
		// 生命周期:
		this.fun()
	},
}
</script>
